const axios = require('axios')

axios.default.baseURL = 'http://localhost:120/'
// axios.default.headers = {
//   "Content-Type": "text/html;charset=utf-8"
// }


// 发送邮箱信息
export const postEmailMessage = (data) => axios.post('/blog/email', { data: data })

// 获取验证码
export const getSvgCaptcha = () => axios.get('/blog/getsvg')

// 用户登录
export const postUserLogin = (data) => axios.post('/blog/login', {data})

// 用户自动登录
export const postAutoLogin = () => axios.post('/blog/autologin')

// 用户退出登录
export const postUserLogout = () => axios.post('/blog/logout')

// 更改用户名
export const postChangeUsername = (data) => axios.post('/blog/changeUsername', data)

// 更改密码
export const postChangePassword = (data) => axios.post('/blog/changePassword', data)

// -------------------------------

// 申请友链
export const postUserLink = (data) => axios.post('/user/userLink', data)

// 获取所有友链列表
export const getLinkList = () => axios.get('/user/getAllLink')

// 获取当前账号的友链信息
export const getLinkById = () =>axios.get('/user/getUserLink')

// 发送留言
export const postMessage = (data) => axios.post('/message/message', data)

// 获取所有留言数据
export const getAllMessage = (val) => axios.get('/message/getAll', {params:{
  skip: val
}})

// -----------------------------------

// 初始化路由
export const initializeAccount = () => axios.get('/root/')

// 登录路由
export const rootLogin = (data) => axios.post('/root/rootlogin',data)

// 修改root密码
export const postRootPassword = (rootPass) => axios.post('/root/rootPassword', rootPass)

// 管理员将友链移入移出推荐友链列表
export const changeHotLink = (data) => axios.post('/root/isHotLink', data)

// 删除友链
export const deleteLink = (data) => axios.delete(`/root/deleteLink`, data)

// 删除选中上传的文件
export const deleteFile = (data) => axios.post('/root/deleteFile', data)

// 删除选中上传的图片
export const deleteArticleImg = (data) => axios.post('/root/deleteImg', data)

// 将所有文章信息传入到数据库中
export const publishArticle = (data) => axios.post('/root/saveArticle',data)

// 获取所有文章列表
export const getArticleList = (num) => axios.get('/root/getArticlelist',{params: {num: num}})

// 获取文章详情
export const getMdVal = (val) => axios.get(`${val}`)

// 修改文章是否推荐的状态
export const postChangeArticleHot = (row) => axios.post('/root/changeArticleHot', row)

// 移除文章
export const deleteArticle = (id) => axios.delete('/root/deleteArticle', {params: {id: id}})

// 提交文章内容修改
export const reviseArticleContent = (data) => axios.post('/root/reviseArticle', data)

// 提交文章标题和简介的修改
export const reviseArticleTitle = (data) => axios.post('/root/revisetitle', data)
